import React, { useState, useRef } from "react";
import classes from "./Checkout.module.css";
import "react-phone-number-input/style.css";
import PhoneInput from "react-phone-number-input";

const isEmpty = (value) => value.trim() === "";
const isPostalCode = (value) => value.trim().length === 5;

const Checkout = (props) => {
  const [value, setValue] = useState();
  const [formInputValidity, setFormInputValidity] = useState({
    firstName: true,
    lastName: true,
    addressLine1: true,
    addressLine2: true,
    city: true,
    postalCode: true,
    phoneNumber: true,
  });

  const firstNameInputRef = useRef();
  const lastNameInputRef = useRef();
  const addressLine1InputRef = useRef();
  const addressLine2InputRef = useRef();
  const cityInputRef = useRef();
  const postalCodeInputRef = useRef();
  const phoneNumberInputRef = useRef();

  const ChechoutHanler = (event) => {
    event.preventDefault();

    const enteredFirstName = firstNameInputRef.current.value;
    const enteredLastName = lastNameInputRef.current.value;
    const enteredAddressLine1 = addressLine1InputRef.current.value;
    const enteredAddressLine2 = addressLine2InputRef.current.value;
    const enteredCity = cityInputRef.current.value;
    const enteredPostalCode = postalCodeInputRef.current.value;
    const enteredPhoneNumber = phoneNumberInputRef.current.value;

    const enteredFirstNameIsValid = !isEmpty(enteredFirstName);
    const enteredLastNameIsValid = !isEmpty(enteredLastName);
    const enteredAddressLine1IsValid = !isEmpty(enteredAddressLine1);
    const enteredAddressLine2IsValid = !isEmpty(enteredAddressLine2);
    const enteredCityIsValid = !isEmpty(enteredCity);
    const enteredPostalCodeIsValid = isPostalCode(enteredPostalCode);
    const enteredPhoneNumberIsValid = !isEmpty(enteredPhoneNumber);

    setFormInputValidity({
      firstName: enteredFirstNameIsValid,
      lastName: enteredLastNameIsValid,
      addressLine1: enteredAddressLine1IsValid,
      addressLine2: enteredAddressLine2IsValid,
      city: enteredCityIsValid,
      postalCode: enteredPostalCodeIsValid,
      phoneNumber: enteredPhoneNumberIsValid,
    });

    const formIsValid =
      enteredFirstNameIsValid &&
      enteredLastNameIsValid &&
      enteredAddressLine1IsValid &&
      enteredAddressLine2IsValid &&
      enteredCityIsValid &&
      enteredPostalCodeIsValid &&
      enteredPhoneNumberIsValid;

    if (!formIsValid) {
      return;
    }
  };

  return (
    <form className={classes.form} onSubmit={ChechoutHanler}>
      <section className={classes.names}>
        <div className={classes.control}>
          <label htmlFor="first-name">First Name</label>
          <input type="text" id="first-name" ref={firstNameInputRef} />
          {!formInputValidity.firstName && <p>Please Enter the First Name</p>}
        </div>
        <div className={classes.control}>
          <label htmlFor="last-name" ref={lastNameInputRef}>
            Last Name
          </label>
          <input type="text" id="last-name" />
          {!formInputValidity.lastName && <p>Please Enter the Last Name</p>}
        </div>
      </section>
      <section className={classes.street}>
        <div className={classes.control}>
          <label htmlFor="street-address">Street Address</label>
          <input
            type="text"
            id="street-address1"
            placeholder="House Number"
            ref={addressLine1InputRef}
          />
          {!formInputValidity.addressLine1 && (
            <p>Please Enter the House Number</p>
          )}
          <input
            type="text"
            id="street-address2"
            placeholder="Street Name"
            ref={addressLine2InputRef}
          />
          {!formInputValidity.addressLine2 && (
            <p>Please Enter the Streeet Name</p>
          )}
        </div>
      </section>
      <section className={classes.names}>
        <div className={classes.control}>
          <label htmlFor="city">Town/City</label>
          <input type="text" id="city" ref={cityInputRef} />
          {!formInputValidity.city && <p>Please Enter the City</p>}
        </div>
        <div className={classes.control}>
          <label htmlFor="postal_code">Postal Code/Zip</label>
          <input type="number" id="postal_code" ref={postalCodeInputRef} />
          {!formInputValidity.postalCode && <p>Please Enter the Postal Code</p>}
        </div>
      </section>
      <div className={classes.control}>
        <label htmlFor="phone_number">Phone Number</label>
        <PhoneInput
          className={classes.phn_control}
          type="tel"
          id="phone_number"
          placeholder="Enter phone number"
          value={value}
          onChange={setValue}
          ref={phoneNumberInputRef}
        />
        {!formInputValidity.phoneNumber && <p>Please Enter the Phone Number</p>}
      </div>
      <div className={classes.action}>
        <button type="button" className={classes.close} onClick={props.onClose}>
          Close
        </button>
        <button className={classes.checkout}>Checkout</button>
      </div>
    </form>
  );
};

export default Checkout;
